<template>
  <g-body-layout
    :pageIndex="pageNumber"
    :pageSize="pageSize"
    :total="total"
    :filterText.sync="filterText"
    @pageIndexChange="pageIndexChange"
    @pageSizeChange="pageSizeChange"
    @suffixSearchCallback="onFilterReady"
  >
    <!-- 按钮组插槽 -->
    <template #btnGroup>
      <g-btn-group :buttonLists="btnList" @btnCallBack="btnGroupClick">
      </g-btn-group>
    </template>
    <!-- g-ndo-table插槽 -->
    <template #table>
      <g-ndo-table
        :id="id"
        :handName="'计量单位'"
        :dataList="dataList"
        :isTableLoading="loading"
        :total="total"
        @rowClick="rowClick"
      ></g-ndo-table>
    </template>
    <!-- 详细内容插槽 -->
    <template #content>
      <create-or-edit-unit-fo-measure
        :ndoEditParams="ndoEditParams"
        :refreshTag="refreshTag"
        :saveTag="saveTag"
        :resetTag="resetTag"
        @childRefresh="childRefresh($event)"
        @rowClick="rowClick($event)"
        v-if="isShowEdit"
      ></create-or-edit-unit-fo-measure>
      <g-empty v-else> </g-empty>
    </template>
  </g-body-layout>
</template>

<script>
import CreateOrEditUnitFoMeasure from "./create-or-edit-unit-fo-measure/create-or-edit-unit-fo-measure.vue";
import GCopyNdo from "@/components/g-copy-ndo/g-copy-ndo.vue";
import { GNdoPageBase } from "@/utils";

export default {
  name: "UnitFoMeasure",
  mixins: [GNdoPageBase],
  components: {
    CreateOrEditUnitFoMeasure,
  },

    data() {
        return {
      isTableLoading: false,
      filterText: "",
    };
  },

  mounted() {},
  created() {
    this.getBtnList();
  },
  methods: {
    getBtnList() {
      // 获取按钮列表
      this.btnList = [
        {
          text: this.AppConst.button.create,
          icon: this.AppConst.icon.create,
          eventName: this.AppConst.button.create,
          display: true,
        },
        {
          text: this.AppConst.button.copy,
          icon: this.AppConst.icon.copy,
          eventName: this.AppConst.button.copy,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.delete,
          icon: this.AppConst.icon.delete,
          eventName: this.AppConst.button.delete,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.refresh,
          icon: this.AppConst.icon.refresh,
          eventName: this.AppConst.button.refresh,
          right: true,
          display: this.isShowEdit,
        },
        {
          text: this.AppConst.button.save,
          icon: this.AppConst.icon.save,
          eventName: this.AppConst.button.save,
          right: true,
          type: "primary",
          display: this.isShowEdit,
        },
      ];
    },
    copy() {
      this.createModal(
        { title: "复制", width: "600px" },
        {
          component: GCopyNdo,
          props: {
            entity: {
              copyValue: this.dataList[0].name,
              value: this.dataList[0].name,
            },
          },
          on: {
            success: (res) => {
              console.log(res);
              // 手动延时
              this.loading = true;
              setTimeout(() => {
                this.loading = false;
                this.copySuccess(res.value, res.copyValue);
                this.refreshGoFirstPage();
              }, 500);
            },
          },
        }
      );
    },
    delete() {
      // 删除确认
      this.$confirm({
        title: "确认删除",
        content: "确认删除该计量单位吗？删除后无法恢复。",
        onOk: () => {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            // 消息提示
            this.deleteSuccess();
            // 刷新
            this.refresh();
          }, 500);
        },
      });
    },
    fetchData(request) {
      this.loading = true;
      setTimeout(() => {
        request.finishedCallback();
        // 填充dataList 示例数据
        let dataList = [];
        const unitTypes = [
          "长度单位", 
          "重量单位", 
          "体积单位", 
          "面积单位", 
          "温度单位",
          "时间单位",
          "电力单位",
          "其他单位"
        ];
        
        const statuses = ["启用", "禁用"];
        
        for (let i = 0; i < 25; i++) {
          const typeIndex = i % 8;
          let symbol, name, baseUnit, conversionRate;
          
          // 根据单位类型生成不同的单位数据
          switch(typeIndex) {
            case 0: // 长度单位
              const lengthUnits = [
                { symbol: "m", name: "米", base: true, rate: 1 },
                { symbol: "cm", name: "厘米", base: false, rate: 100 },
                { symbol: "mm", name: "毫米", base: false, rate: 1000 },
                { symbol: "km", name: "千米", base: false, rate: 0.001 },
                { symbol: "in", name: "英寸", base: false, rate: 39.37 }
              ];
              const lengthIndex = i % 5;
              symbol = lengthUnits[lengthIndex].symbol;
              name = lengthUnits[lengthIndex].name;
              baseUnit = lengthUnits[lengthIndex].base;
              conversionRate = lengthUnits[lengthIndex].rate;
              break;
            
            case 1: // 重量单位
              const weightUnits = [
                { symbol: "kg", name: "千克", base: true, rate: 1 },
                { symbol: "g", name: "克", base: false, rate: 1000 },
                { symbol: "mg", name: "毫克", base: false, rate: 1000000 },
                { symbol: "t", name: "吨", base: false, rate: 0.001 },
                { symbol: "lb", name: "磅", base: false, rate: 2.20462 }
              ];
              const weightIndex = i % 5;
              symbol = weightUnits[weightIndex].symbol;
              name = weightUnits[weightIndex].name;
              baseUnit = weightUnits[weightIndex].base;
              conversionRate = weightUnits[weightIndex].rate;
              break;
            
            case 2: // 体积单位
              const volumeUnits = [
                { symbol: "m³", name: "立方米", base: true, rate: 1 },
                { symbol: "L", name: "升", base: false, rate: 1000 },
                { symbol: "mL", name: "毫升", base: false, rate: 1000000 },
                { symbol: "cm³", name: "立方厘米", base: false, rate: 1000000 },
                { symbol: "gal", name: "加仑", base: false, rate: 264.172 }
              ];
              const volumeIndex = i % 5;
              symbol = volumeUnits[volumeIndex].symbol;
              name = volumeUnits[volumeIndex].name;
              baseUnit = volumeUnits[volumeIndex].base;
              conversionRate = volumeUnits[volumeIndex].rate;
              break;
            
            case 3: // 面积单位
              const areaUnits = [
                { symbol: "m²", name: "平方米", base: true, rate: 1 },
                { symbol: "cm²", name: "平方厘米", base: false, rate: 10000 },
                { symbol: "km²", name: "平方千米", base: false, rate: 0.000001 },
                { symbol: "ha", name: "公顷", base: false, rate: 0.0001 },
                { symbol: "mu", name: "亩", base: false, rate: 0.0015 }
              ];
              const areaIndex = i % 5;
              symbol = areaUnits[areaIndex].symbol;
              name = areaUnits[areaIndex].name;
              baseUnit = areaUnits[areaIndex].base;
              conversionRate = areaUnits[areaIndex].rate;
              break;
            
            case 4: // 温度单位
              const tempUnits = [
                { symbol: "℃", name: "摄氏度", base: true, rate: 1 },
                { symbol: "℉", name: "华氏度", base: false, rate: 1.8 },
                { symbol: "K", name: "开尔文", base: false, rate: 1 }
              ];
              const tempIndex = i % 3;
              symbol = tempUnits[tempIndex].symbol;
              name = tempUnits[tempIndex].name;
              baseUnit = tempUnits[tempIndex].base;
              conversionRate = tempUnits[tempIndex].rate;
              break;
            
            case 5: // 时间单位
              const timeUnits = [
                { symbol: "s", name: "秒", base: true, rate: 1 },
                { symbol: "min", name: "分钟", base: false, rate: 1/60 },
                { symbol: "h", name: "小时", base: false, rate: 1/3600 },
                { symbol: "d", name: "天", base: false, rate: 1/86400 }
              ];
              const timeIndex = i % 4;
              symbol = timeUnits[timeIndex].symbol;
              name = timeUnits[timeIndex].name;
              baseUnit = timeUnits[timeIndex].base;
              conversionRate = timeUnits[timeIndex].rate;
              break;
            
            case 6: // 电力单位
              const powerUnits = [
                { symbol: "W", name: "瓦特", base: true, rate: 1 },
                { symbol: "kW", name: "千瓦", base: false, rate: 0.001 },
                { symbol: "MW", name: "兆瓦", base: false, rate: 0.000001 },
                { symbol: "HP", name: "马力", base: false, rate: 0.00134 }
              ];
              const powerIndex = i % 4;
              symbol = powerUnits[powerIndex].symbol;
              name = powerUnits[powerIndex].name;
              baseUnit = powerUnits[powerIndex].base;
              conversionRate = powerUnits[powerIndex].rate;
              break;
            
            case 7: // 其他单位
              const otherUnits = [
                { symbol: "pcs", name: "件", base: true, rate: 1 },
                { symbol: "doz", name: "打", base: false, rate: 1/12 },
                { symbol: "box", name: "箱", base: false, rate: 1/24 }
              ];
              const otherIndex = i % 3;
              symbol = otherUnits[otherIndex].symbol;
              name = otherUnits[otherIndex].name;
              baseUnit = otherUnits[otherIndex].base;
              conversionRate = otherUnits[otherIndex].rate;
              break;
          }
          
          dataList[i] = {
            id: i + 1,
            unitCode: `UM${String(i + 1).padStart(4, "0")}`,
            name: name,
            symbol: symbol,
            unitType: unitTypes[typeIndex],
            baseUnit: baseUnit,
            conversionRate: conversionRate,
            standardInfo: baseUnit ? "国际标准单位(SI)" : "非SI单位",
            description: `${name}(${symbol})是${unitTypes[typeIndex]}，${baseUnit ? '是基本单位' : '换算率为' + conversionRate}`,
            status: statuses[i % 2],
            createdBy: "admin",
            createdTime: "2024-03-19 " + (10 + i % 10) + ":" + (i % 60),
          };
        }

        // filter 过滤
        if (this.filterText) {
          console.log(this.filterText);
          dataList = dataList.filter((item) => {
            return item.unitCode.indexOf(this.filterText) > -1 || 
                   item.name.indexOf(this.filterText) > -1 ||
                   item.symbol.indexOf(this.filterText) > -1 ||
                   item.unitType.indexOf(this.filterText) > -1 ||
                   item.standardInfo.indexOf(this.filterText) > -1 ||
                   item.description.indexOf(this.filterText) > -1;
          });
        }

        request.successCallback({
          items: dataList,
          totalCount: dataList.length * 2,
        });
      }, 1000);
    },
    },
};
</script>

<style lang="scss" scoped></style>